img {
  max-width: 100%; }

img.logo {
	height: 65px;
	margin: 10px 0 0 39px; }

section img {
	width: auto;
	max-height: 65vh;
	border-radius: 4px; }

.caption /* Use captions on standalone section images */ {
  color: #999;
  font-family: 'Vollkorn', serif;
  font-size: 0.94rem;
  margin-bottom: 30px;
  margin-left: 4px; }

@media screen and (max-width: 739px) {
  .caption {
    margin-top: -10px /* Align caption a bit tighter to image on mobile displays */; } }


// Lightbox adapted from Greg Bowler on Code Pen: https://codepen.io/g105b/pen/xuclB
.thumbnail {
  padding: 0;
  display: inline-block;
  margin: 1px 3px;

  &:hover {
    opacity: 0.7; }

  img {
    display: block;
    height: auto; } }

@media screen and (max-width: 739px) {
  .thumbnail {
    margin: 0 /* Reset for mobile display so that captions underneath thumbnail and regular section images have the same spacing */; } }

.lightbox {
  display: none;

  &:target {
    display: block; }

  a {
    position: fixed;
    display: block;
    background: #000;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: hidden;
    z-index: 8;

    &::after {
      content: "\2715";
      position: fixed;
      z-index: 10;
      top: 0;
      right: 0;
      text-align: right;
      font-size: 2.5em;
      font-weight: lighter;
      padding: 5px 25px 0 0;
      color: #fff; }

    img {
      display: block;
      z-index: 8;
      width: auto;
      max-height: 100vh;
      height: 100vh;
      margin: auto;
      border-radius: 0; } } }


.lightbox-caption {
	position: fixed;
	z-index: 11;
	bottom: 0;
	right: 0;
	width: 100vw;
	height: auto;
	text-align: center;
	background: rgba(0, 0, 0, 0.8);
	color: #fff;
	padding: 20px; }

@media screen and (max-width: 739px) {
  .lightbox-caption {
    padding: 10px;
    font-size: 0.8rem; } }


@-webkit-keyframes Show {
  from {
    opacity: 0;
    zoom: 2; } }




